import React, { useState, useEffect } from "react";
import axios from "axios";
import { useSearchParams } from "react-router-dom";
import uuid from "react-uuid";
import allActor from "../assets/images/img_all_actor.png";

const ReservationDone = () => {
  const [searchParams] = useSearchParams(); // setSearchParams 제거
  const bookId = searchParams.get("bookId");
  const [name, setName] = useState("");
  const [peopleNumber, setPeopleNumber] = useState("");
  const [selectedSchedule, setSelectedSchedule] = useState("");
  const [currentUuid, setCurrentUuid] = useState("");

  useEffect(() => {
    if (bookId && currentUuid) {
      // 이미 호출된 경우 방지
      getReservationInfo();
    }
  }, [bookId, currentUuid]); // 의존성 배열에 hasFetched 추가

  useEffect(() => {
    const getUuid = localStorage.getItem("uuid");
    if (getUuid) {
      setCurrentUuid(getUuid);
    } else {
      const newUuid = uuid();
      localStorage.setItem("uuid", newUuid);
      setCurrentUuid(newUuid);
    }
  }, []);

  const getReservationInfo = async () => {
    try {
      const response = await axios.get(
        `https://rocklord-srofxtylkq-du.a.run.app/v1/tickets/${bookId}`,
        {
          headers: {
            "X-Custom-Header": "7YK57Jik6529",
            "X-Client-ID": currentUuid,
          },
        }
      );
      console.log("response!!", response);
      setName(response?.data?.bookName);
      setPeopleNumber(response?.data?.bookQuantity);
      setSelectedSchedule(response?.data?.showSequence);
    } catch (error) {
      console.error("Error fetching reservation info:", error);
    }
  };

  const infos = [
    "좌석은 공연 시작 1시간 전부터 티켓부스에서 선착순으로 선택할 수 있습니다.",
    "물 외에 모든 음식은 공연장 내 반입금지입니다.",
    "사전 협의 되지 않은 모든 촬영, 녹음은 금지입니다. 단, 커튼콜은 촬영 가능합니다.",
    "공연장 : 서대문 문화체육회관 대극장 (서울 서대문구 백련사길 39)",
    "무료 주차 : 서대문 문화회관 신관 및 본관의 지하 주차장, 서대문여성이룸센터 주차장 (단, 주차공간이 부족할 수 있으니 대중교통 이용을 권장합니다.)",
  ];
  const schedule = {
    1: "1월 25일(토) 12시 30분",
    2: "1월 25일(토) 15시 30분",
    3: "1월 25일(토) 18시 30분",
    4: "1월 26일(일) 12시 00분",
    5: "1월 26일(일) 15시 00분",
    6: "1월 26일(일) 18시 00분",
  };

  return (
    <div style={{ display: "flex", flexDirection: "column", width: "100%" }}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          width: "100%",
        }}
      >
        <div
          style={{
            marginTop: 52,
            marginBottom: 38,
          }}
        >
          <img src={allActor} alt="allActor" width={"100%"} />
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            textAlign: "center",
            marginBottom: 60,
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              marginBottom: 10,
              textAlign: "center",
              justifyContent: "center",
            }}
          >
            <text
              style={{
                fontSize: 20,
                fontWeight: 600,
                marginRight: 6,
              }}
            >
              {`${name}님 `}
            </text>
            <text
              style={{
                fontSize: 20,
                fontWeight: 500,
                marginRight: 6,
              }}
            >
              {`스쿨오브락`}
            </text>
            <text
              style={{
                fontSize: 20,
                fontWeight: 600,
                color: "#B71C1C",
              }}
            >
              {`${selectedSchedule}`}
            </text>
            <text
              style={{
                fontSize: 20,
                fontWeight: 600,
                marginRight: 6,
              }}
            >
              {`회차 `}
            </text>
            <text
              style={{
                fontSize: 20,
                fontWeight: 500,
              }}
            >
              {`공연`}
            </text>
          </div>
          <text
            style={{
              fontSize: 20,
              fontWeight: 600,
              marginBottom: 10,
              color: "#B71C1C",
            }}
          >
            {`(${schedule[selectedSchedule]})`}
          </text>

          <div
            style={{
              display: "flex",
              flexDirection: "row",
              textAlign: "center",
              justifyContent: "center",
            }}
          >
            <text
              style={{
                fontSize: 20,
                fontWeight: 500,
                marginRight: 6,
              }}
            >
              {`총`}
            </text>
            <text
              style={{
                fontSize: 20,
                fontWeight: 600,
                color: "#B71C1C",
              }}
            >
              {`${peopleNumber}`}
            </text>
            <text
              style={{
                fontSize: 20,
                fontWeight: 600,
                marginRight: 6,
              }}
            >
              {`장`}
            </text>
            <text style={{ fontSize: 20, fontWeight: 500 }}>
              {`예매 완료되었습니다.`}
            </text>
          </div>
          <div style={{ marginTop: 16 }}>
            <text
              style={{
                marginLeft: "auto",
                color: "#666666",
                fontSize: 14,
                fontWeight: 500,
              }}
            >
              {"※예매확인을 위해 화면을 꼭 캡쳐해주세요"}
            </text>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            flexDirection: "column",
            paddingLeft: 30,
            paddingRight: 30,
          }}
        >
          <text style={{ fontSize: 18, fontWeight: 500, marginBottom: 12 }}>
            공연 주의사항
          </text>
          <div style={{ display: "flex", flexDirection: "column" }}>
            {infos.map((info, index) => (
              <div
                key={`info-${index}`}
                style={{
                  display: "flex",
                  flexDirection: "row",
                }}
              >
                <div
                  style={{
                    width: 4,
                    height: 4,
                    backgroundColor: "black",
                    borderRadius: 100,
                    margin: "9px 9px 0 9px",
                  }}
                />
                <div style={{ flex: 1 }}>
                  <text
                    style={{
                      fontSize: 16,
                      fontWeight: 400,
                      lineHeight: "24px",
                    }}
                  >
                    {info}
                  </text>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ReservationDone;
