    "1-12":
      "여긴 호레이스 그린\n우리의 목표 오직 하나뿐 좋은 대학\n성적과 등수로 아이들을 나누죠\n근데 대리교사 계속 분위기를 흐리죠 수상하게\n학생들도 묘한 눈빛을 주고받아\n\n여기 호레이스 그린 금지된 일들\n장난치고 깔깔대는 것\n수업시간에 수근대는 것\n그가 오고 시작됐죠\n\n슈니블리 타고오는 차 봤나요\n 물론이죠! 내 옆자리!\n벌거벗은 여자그림 그려놨죠\n거지같은 옷차림도 끔찍하죠\n싸구려에! 낡은 셔츠!\n구멍 뚫린 양말 술에 쩔은 눈\n\n건방지고 예의없는 태도까지\n떡진 머리! 땀 냄새도!\n한심한걸 말하자면 입 아프죠\n 교실에서 들리는 건 노랫소리\n시끄러워! 또 있어요!\n아이들이 우리보다 더 좋아해\n\n여긴 호레이스 그린\n완벽한 규칙 어떤 것도 예외는 없어\n그가 온 뒤로 모두 변했죠\n웃음 소리 계속 들려\n여긴 호레이스 그린\n여긴 호레이스 그린\n\n여긴 호레이스 그린\n엄격한 규칙 우린 항상 지켜왔지만\n그 선생처럼 따라해보면\n\n혹시 뭔가 달라질까\n혹시 좀 더 좋아질까",
    "1-13":
      "온 세상이 너를 손가락질할 때\n미쳐버릴 만큼 널 화나게 할 때\n유일한 방법은 맞서 싸우기\n주저말고 지금\n꺼지라 말해!\n\n고함을 쳐 악을 질러\n사나운 발톱을 세워\n니가 원하는 그걸\n외쳐봐 다시 한번\n세상 끝까지 들릴 수 있게\n꺼지라 말해!\n\n짜증나는 시선 (꺼지라 말해!)\n틀에 박힌 세상 (꺼지라 말해!)\n무관심과 차별 (꺼지라 말해!)\n이거나 드시지 (꺼지라 말해!)\n꼰대들의 참견 (꺼지라 말해!)\n자, 이제 다 일어나 (꺼지라 말해!)\n\n고함을 쳐 악을 질러\n사나운 발톱을 세워\n네가 원하는 그걸\n외쳐봐 다시 한번\n세상 끝까지 들릴 수 있게\n꺼지라 말해!\n\n다이어트 강요할 때 (꺼지라 말해!)\n컴퓨터 못하게 할 때 (꺼지라 말해!)\n내 방문 멋대로 열 때 (꺼지라 말해!)\n현대 미디어 속에서 여성미의\n비현실적 이미지를 강조하는 걸 봤을 때!\n꺼지라 말해!\n\n틀을 부셔 불을 질러\n정지선을 가로 질러\n네가 원하는 그걸\n외쳐봐 다시 한 번\n세상 끝까지 들릴 수 있게\n꺼지라 말해!\n\n자, 수업을 시작해 보실까\n스쿨 오브 락\n온 세상이 너를 손가락질할 때\n미쳐버릴 만큼 널 화나게 할 때\n유일한 방법은 맞서 싸우기\n주저말고 지금 꺼지라 말해\n\n고함을 쳐 악을 질러\n사나운 발톱을 세워\n틀을 부셔 불을 질러\n정지선을 가로질러\n\n남이 정해준 길을 가지마 (예!)\n이제 우리가 가르쳐주마 (예!)\n한심한 개소리들 듣지마\n꺼지라 말해! 꺼지라 말해! 꺼지라 말해!\n\n꺼지라 말해!!!",
    "2-1":
      "자, 슈니블리 선생님이 회의에 가셨으니\n이제부턴 내 말을 따르도록 해\n\n너! 지금 뭐 해 움직여\n(밴드 배틀까지 36시간 남았어!)\n너! 문을 지켜 철저히\n(오케이! 교실 보안 이상 무!)\n망설일 시간 없어\n모두 우릴 위한 일\n자, 지금부터 집중!\n\n잡담 금지 핸드폰도 금지\n모두 역할 알지\n이제 연습할 시간\n책상 치워 교과서도 치워\n악기 준비됐지\n이제 연습할 시간\n\n너! 조명 세팅 시작해\n(3번 핀 최대로 밝게)\n너! 멋진 안무 준비해\n(응! Five Six Seven Eight)\n반항적인 태도에 건방진 눈빛까지\n자, 준비됐지? Let's Go!\n\n삐딱하게 서서 흔들어봐\n시크하게 웃어\n이제 우리의 시간\n손을 높게 들어 흔들어봐\n시끄럽게 뛰어\n이제 우리의 시간\n\n마이크는 준비됐어? (응!)\n무대장치는? (응!)\n모두 내가 시킨 대로 준비돼야 해\n폭죽도 준비됐어? (확인~)\n확실하게 해! (응!)\n잘할 수 있지 소피?\n케이블 밟지 마! (알아~)\n\n무전기 챙긴 사람?\n제발! 이게 어려워?\n필요한 건 다들 엄마카드 쓰자구 (응~)\n노래리스트는? 준비됐겠지? (어..)\n만약 실수하면 너 진짜 가만 안 둬\n이펙터는? (넵) 피크? (넵)\n앰프랑 페달은? (넵)\n한 명 실수하면 너네 둘다 죽었어 (으응..)\n노래하기 전엔 목소리도 아껴 (응!)\n이제 다 됐어. 딱 하나만 빼고!\n\n너! 준비됐어? 무대의상 (거의 다 됐어..)\n하! 뭘 꾸물대! 보여줘 (그렇게 쉬운게 아니라구!)\n야! 우리 시간 없어\n이제 결정해야 해 (좋아 보여줄게)\n최악!!!\n\n드럼 계속 달려 멈추지마\n귀에 피가 나게\n이제 우리의 시간\n모두 계속 달려 멈추지마\n우린 이길 거야\n\n이제 우리의 시간\n모두 환호할 시간\n이제 보여줄 시간\n\n이제 우리의 시간!",
